<template>
  <div class="hello">
    <header>
      <h1>{{ msg }}</h1>
    </header>
    <h3>上の段</h3>
    <div class="input_text">
      <input class="form-control" id="title_top" value="はじめての">
    </div>
    <h3>下の段</h3>
    <div class="input_text">
      <input class="form-control" id="title_bottom" value="懇親会LTだゾ">
    </div>
    <div class="row">
      <div class="col-sm-6">
        <p>左側</p>
        <input type="text" class="form-control" id="left_cap" value="脚本 : フロント 太郎">
      </div>
      <div class="col-sm-6">
        <p>左側</p>
        <input type="text" class="form-control" id="right_cap" value="監督 : エンド 次郎">
      </div>
    </div>
    <div class="submit">
      <button type="button" class="btn btn-success" id="enter">嵐を呼ぶ!</button>
      <button type="button" class="btn btn-success" id="tweet">嵐をシェアする!</button>
    </div>
    <div class="output">
      <canvas id="output_field" width="2880" height="1620"></canvas>
      <img id="output_image" />
    </div>
    <footer>
      <p>poewred by ITCreateClub</p>
    </footer>
  </div>
</template>
<script src="https://code.jquery.com/jquery-3.3.1.min.js" integrity="sha256-FgpCb/KJQlLNfOu91ta32o/NMZxltwRo8QtmkMRdAu8=" crossorigin="anonymous"></script>
<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: '嵐を呼ぶサブタイトルジェネレータ'
    }
  }
}
$(function(){
  // 背景グラデ
  fillBack();

  $('#enter').click(function(){
    var ctx = document.getElementById("output_field").getContext("2d");
    ctx.clearRect(0, 0, 2880, 1620);
    fillBack();
    // 嵐部分
    ctx.font = '250px sans-serif';
    ctx.textAlign = 'left';
    var top_text = $('#title_top').val();
    var bottom_text = $('#title_bottom').val();
    ctx.fillStyle = '#5853A0';
    ctx.fillText(top_text,140,590);
    ctx.textAlign = 'right';
    ctx.fillText(bottom_text,2740,930);

    // 左キャプション
    var left_cap = $('#left_cap').val();
    ctx.textAlign = 'left';
    ctx.lineWidth = 5;
    ctx.fillStyle = "#FFF";
    ctx.font = "90px cursive";
    ctx.fillText(left_cap, 400, 1350);
    ctx.strokeStyle = "gray";
    ctx.strokeText(left_cap, 400, 1350);

    // 右キャプション
    var right_cap = $('#right_cap').val();
    ctx.textAlign = 'right';
    ctx.lineWidth = 5;
    ctx.fillStyle = "#FFF";
    ctx.font = "90px cursive";
    ctx.fillText(right_cap, 2480, 1350);
    ctx.strokeStyle = "gray";
    ctx.strokeText(right_cap, 2480, 1350);

    // 生成
    var ctx = document.getElementById("output_field");
    var png = ctx.toDataURL();
    document.getElementById("output_image").src = png;
    $('#output').css('border', '0');
    $('#output_field').hide();
    $('#output_image').show();
  });
});
function fillBack(){
  // 背景グラデ
  var ctx_base = document.getElementById("output_field").getContext("2d");
  ctx_base.beginPath();
  var ctx_base_color = ctx_base.createLinearGradient(0, 410, 0, 1620);
  ctx_base_color.addColorStop(0.0, '#FD81A6');
  ctx_base_color.addColorStop(1.0, '#FFFF83');
  ctx_base.fillStyle = ctx_base_color;
  ctx_base.fillRect(0, 0, 2880, 1620);
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
p{
  margin: 0;
}
header{
  border-bottom: 1px solid gray;
}
footer{
  margin-top: 25px;
  border-top: 1px solid gray;
}
h1{
  margin:0;
}
.input_text{
  max-width: 960px;
  margin: 0 auto;
}
.input_text input{
  margin: 0 0 20px 0;
}
h3{
  margin: 0 auto;
  text-align: left;
  max-width: 960px;
}
.row{
  max-width: 960px;
  margin: 0 auto;
}
.row div{
  padding: 0;
}
.row p{
  text-align: left;
  margin-bottom: 0;
}
.submit{
  max-width: 960px;
  margin: 0 auto;
  margin-bottom: 15px;
  text-align:right;
}
.submit #enter, .submit #tweet{
  margin-top: 20px;
  width: 100%;
}
#tweet{
  background-color: royalblue;
  border-color: mediumblue;
}
#tweet:hover{
  background-color: #3453b3;
}
.output{
  width: 960px;
  height: 540px;
  margin: 0 auto;
  border: 1px solid gray;
}
.output #output_field{
  width: 100%;
  height: 100%;
  /*background: linear-gradient(0deg,#FFFF83,#FD81A6);*/
  font-size: 50px;
}
.output #output_image{
  max-width: 960px;
  display: none;
}
footer{
  height: 30px;
}
</style>



